.card{
    background: #FFFFFF;
    box-shadow: 0 0 1.25rem rgba(0, 0, 0, 0.1);
    border-radius: 1.875rem;
    padding: 0.937rem 1.25rem 1.875rem 1.25rem;
    position: relative;
}

.heart{
    display: block;
}

.painted{
    display: none;
}

.favourite .heart{
    display: none;
}

.favourite .painted{
    display: block;
}

.hearts{
    position: absolute;
    top: 0.937rem;
    left: 0.937rem;
}

.header{
    height: 15.625rem;
    width: 100%;
    margin-bottom: 3.125rem;
}

.header img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.body{
    margin-top: 1.25rem;
    display: flex;
    justify-content: space-between;
}

.body .title{
    text-align: start;
}

.star{
    margin-top: 1.562rem;
    display: flex;
}

.number{
    color: #838383;
}

.cost,
.lastPrice{
    color: #FFA542;
}

.lastPrice{
    font-size: 0.812rem;
    line-height: 1.230em;
    text-decoration-line: line-through;
    text-align: center;
}

.discountItem{
    display: none;
    color: #DF6464;
    font-size: 1rem;
    line-height: 1.25em;
}

.discountItem .discount{
    display: block;
}

.discount .cost,
.discount .lastPrice{
    color: #DF6464;
}

.firm{
    position: absolute;
    top: 1.25rem;
    right: 1.25rem;
}