.header{
    padding: 0.937rem 0;
    margin-bottom: 1.562rem;
}

.wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.models{
    display: flex;
    align-items: center;
    cursor: pointer;
}

.text{
    font-weight: 500;
    font-size: 0.937rem;
    line-height: 1.2em;
    margin-left: 0.937rem;
    color: #101010;
}

.arrow{
    margin-left: 0.312rem;
}

.other{
    align-items: center;
    display: flex;
}

.phone{
    margin-right: 0.937rem;
}

.nav{
    display: flex;
}

.item{
    display: block;
    margin-right: 1.275rem;
}

.item:last-child{
    margin-right: 0;
}

.languages{
    cursor: pointer;
    display: flex;
    align-items: center;
}

.heart,
.cart{
    position: relative;
}

.count{
    position: absolute;
    top: 0;
    right: 0;
    background: red;
    transform: translate(50%, -50%);
    width: 1.093rem;
    height: 1.093rem;
    background: #FFA542;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    font-size: 0.937rem;
    line-height: 1.2em;
    color: #FFFFFF;
}

.name{
    margin-left: 0.312rem;
    font-weight: 500;
    font-size: 0.937rem;
    line-height: 1.2em;
    color: #838383;
}

.other>div {
    margin-right: 1.562rem;
}

.other>div:last-child{
    height: 9.375rem;
}

.phone{
    margin-right: 0.937rem;
}