.heart{
    display: block;
}

.painted{
    display: none;
}

.favourite .heart{
    display: none;
}

.favourite .painted{
    display: block;
}

.info{
    display: flex;
    margin-bottom: 0.9375rem;
}

.star{
    margin: 0 1.25rem;
    display: flex;
}

.cost{
    display: flex;
}

.sum{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-right: 0.3125rem;
}

.price{
    white-space: nowrap;
    font-size: 1.25rem;
    line-height: 1.2em;
}

.sum .price{
    white-space: nowrap;
    font-size: 1.25rem;
    line-height: 1.2em;
    color: #DF6464;
}

.sum .lastPrice{
    white-space: nowrap;
    font-size: 0.9375rem;
    line-height: 1.2em;
    color: #AAAAAA;
    position: relative;
}

.sum .lastPrice::before{
    position: absolute;
    content: "";
    top: 50%;
    left: 0;
    height: 0.125rem;
    width: 100%;
    background-color: red;
    transform: translateY(-50%);
}

.discount{
    color: #DF6464;
}

.item{
    background: #FFFFFF;
    box-shadow: 0px 0px 1.25rem rgba(0, 0, 0, 0.1);
    border-radius: 1.875rem;
    height: 6.25rem;
    width: 6.25rem;
    display: flex;
    overflow: hidden;
    justify-content: center;
    align-items: center;
}

.item img{
    width: 100%;
    object-fit: cover;
}

.review{
    display: flex;
    justify-content: space-between;
    background: #FFFFFF;
    box-shadow: 0px 0px 1.25rem rgba(0, 0, 0, 0.1);
    border-radius: 0.9375rem;
    padding: 1.25rem;
}

.span{
    display: flex;
    align-items: center;
}

.starReview{
    display: flex;
    margin: 0 1.5625rem;
}

.mark{
    color: #838383;
    margin-left: 0.625rem;
}

.text{
    font-size: 1.25rem;
    line-height: 1.2em;
}

.wrapper{
    padding-right: 1.25rem;
}

.photos{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 0.625rem;
    margin-bottom: 1.25rem;
}

.mainPhoto{
    background: #FFFFFF;
    box-shadow: 0px 0px 1.25rem rgba(0, 0, 0, 0.1);
    border-radius: 1.875rem;
    height: 20.1875rem;
    width: 20.1875rem;
    display: flex;
    justify-content: center;
    align-items: center;
    grid-row: 1/4;
    grid-column: 2/5;
    margin: 0 0 0.625rem 0.625rem;
}

.mainPhoto img{
    max-width: 300px;
}


.gfg{
    gap: 1.25rem;
    margin-bottom: 1.25rem;
}




.otherPhotos{
    display: flex;
    flex-direction: column;
    gap: 0.625rem;
}