.description{
    padding-top: 3.75rem;
    width: 100%;
}

.buttons{
    display: flex;
    justify-content: space-between;
    margin-bottom: 1.875rem;
}

.buttons .btn{
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 15px;
    padding-right: 15px;
}

.text{
    color: #fff;
}

.img{
    margin-right: 0.625rem;
}

.info{
    background: #FFFFFF;
    box-shadow: 0px 0px 1.25rem rgba(0, 0, 0, 0.1);
    border-radius: 0.9375rem;
}

.title{
    box-shadow: 0px 0px 1.25rem rgba(0, 0, 0, 0.1);
    border-radius: 0.9375rem;
    font-size: 1.25rem;
    line-height: 1.2em;
    padding: 1.25rem 1.0625rem;
}

.list{
    padding: 1.25rem 1.0625rem;
}

.item{
    display: flex;
    margin-bottom: 0.625rem;
}

.span{
    margin-right: 0.3125rem;
}