.wrapper{
    background: #fff;
    box-shadow: 0 0 1.25rem rgba(0, 0, 0, 0.1);
    border-radius: 1.875rem 1.875rem 0 0;
    padding: 1.875rem;
    display: flex;
    justify-content: space-between;
}

.nav,
.inf{
    display: flex;
    flex-direction: column;
}

.href,
.condition{
    margin: 0.312rem 0;
    font-weight: 400;
}

.languages{
    margin-top: 2.187rem;
}

.languages,
.kinds{
    display: flex;
}

.name{
    margin: 0 0.468rem;
    cursor: pointer;
    transition: .5s color;
}

.name:hover{
    color: #FFA542;
}

.item{
    margin-right: 1.25rem;
    display: block;
}

.item:last-child{
    margin-right: 0;
}

.social{
    display: flex;
}