.btn{
  display: block;
  background: #101010;
  box-shadow: 0 0 0.9375rem rgba(0, 0, 0, 0.5);
  border-radius: 0.625rem;
  text-align: center;
  color: #FFFFFF;
  cursor: pointer;
}

.btn_mini{
  padding: 0.687rem 0;/*11*/
  font-weight: 500;
  font-size: 0.937rem;
  line-height: 1.2em;
  min-width: 10rem;
}
.btn_big{
  padding: 1.562rem 0;/*25*/
  width: 100%;
}

.green{
  background-color: #43D854;
}